import React, { Component } from 'react';
import Nav from './components/Navbar/Nav.js'
import SideDrawer from './components/Navbar/SideDrawer.js';
import Home from './components/Home/Home.js';
import About from './components/About/About.js';
import Footer from './components/Footer/Footer.js'
import { BrowserRouter, Route } from 'react-router-dom';
import Price from './components/Price/Price.js'
import Treatments from './components/Treatments/Treatments.js'
import Appointment from './components/Appointment/Appointment.js'

class App extends Component {
  state = {
    toggle :false
  }
  ToggleMenuClickHandle = () =>{
    this.setState((prevState)=>{
      return {toggle : !prevState.toggle}
    })
  }
  render() {
    return (
      <BrowserRouter>
        <div className="App">
          <Nav ToggleMenuClickHandle = {this.ToggleMenuClickHandle} show = {this.state.toggle}/>
          <SideDrawer ToggleMenuClickHandle = {this.ToggleMenuClickHandle} show = {this.state.toggle}/>
          <Route exact path='/' component ={Home} />
          <Route exact path='/About' component = {About} />
          <Route exact path ='/Price-List' component = {Price} />
          <Route exact path='/Home' component ={Home} />
          <Route exact path='/Treatments' component ={Treatments} />
          <Route exact path='/Appointment' component ={Appointment} />

          <Footer />
        </div>  
      </BrowserRouter>
    );
  }
}

export default App;

