import React from 'react';
import MenuContent from './MenuContent.js'

const SideDrawer = (props) =>{
    let {show} = props;
    let {ToggleMenuClickHandle} =props;
    let menu_sideDrawer = 'menu-sideDrawer'
    if(show){
        menu_sideDrawer = 'menu-sideDrawer open'
    }
    return (
        <div className={menu_sideDrawer}>
            <MenuContent show = {show} ToggleMenuClickHandle= {ToggleMenuClickHandle} />
         </div>
    )
};

export default SideDrawer;