import React from 'react';
import MenuContent from './MenuContent.js'
import './NavBar.css';

const Nav = (props) =>{
    let {ToggleMenuClickHandle} = props;
    let {show} = props;
    let hamburger_menu = 'hamburger-menu'
    if(show){
        hamburger_menu = 'hamburger-menu open'
    }
    return (
        <div className="navbar">
            <header className='header'>
                <div className={hamburger_menu} onClick={ToggleMenuClickHandle}>
                     <i className="fas fa-bars"></i>
                     <i className="fas fa-times"></i>
                </div>
                <div className="logo">Logo</div>
                <MenuContent show={show} ToggleMenuClickHandle = {ToggleMenuClickHandle} />
            </header>
        </div>
    )
}

export default Nav;