import React from 'react';
import {Link, NavLink, withRouter} from 'react-router-dom';

const MenuContent = (props) =>{
  let {show} = props;
  let {ToggleMenuClickHandle} = props;

      let menu = 'menu'
      if(show){
          menu = 'menu open'
  }
 
    return (
        <div className={menu}>
                    <ul className="menu-wrapper">
                        <li className="links"><NavLink to ='/' onClick={ToggleMenuClickHandle}>Home</NavLink></li>
                        <li className="links"><NavLink to ='/About'onClick={ToggleMenuClickHandle}>About Us</NavLink></li>
                        <li className="links"><NavLink to ='/Price-List' onClick={ToggleMenuClickHandle}>Price-List</NavLink></li>
                        <li className="links"><NavLink to ='/Treatments' onClick={ToggleMenuClickHandle}>Treatments</NavLink></li>
                        <li className="links"><NavLink to ='/Appointment' onClick={ToggleMenuClickHandle}>Appointment</NavLink></li>
                        <li className="links"><NavLink to ='/Contact' onClick={ToggleMenuClickHandle}>Contact</NavLink></li>
                    </ul>
         </div>
    )
}; 

export default withRouter( MenuContent);